body{
    background: $body-color;
}

.section{
    padding: 100px 0;
    &-title{
        h2{
            margin-bottom: 30px;
        }
        p{
            width: 50%;
            margin: 0 auto;
        }
        margin-bottom: 50px;
    }
}

.mbn-200{
    transform: translateY(200px);
    @include desktop-lg{
        transform: translateY(100px);
    }
    @include desktop{
        transform: translateY(50px);
    }
    @include tablet{
        transform: translateY(25px);
    }
}

.form-control{
    padding: 18px 20px;
}

// CTA Styles
.cta{
    padding-top: 100px;
    @include tablet{
        padding-top: 50px;
    }
}
.cta-tagline{
    font-size: 30px;
    margin-bottom: 45px;
    span{
        color: #f65d67;
    }
    @include mobile-xs{
        font-size: 25px;
    }
}