// 1.2. Button Styles
.btn{
    font-size: 16px;
    border-radius: 0;
    padding: 18px 55px;
    border: none;
    letter-spacing: 1px;
    border-radius: 4px;
}

.btn-primary{
    background: $primary-color;
    color: $white;
    position: relative;
    z-index: 1;
}
.btn-primary-gradient{
    background-image: $primary-gradient;
    color: $white;
    position: relative;
    z-index: 1;
    &:hover{
        color: $white;
        box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.05);
    }
}

.btn-secondary{
    background-color: $secondary-color;
    font-size: 14px;
    &:hover{
        background-color: darken($color: $secondary-color, $amount: 5);
    }
}

.btn-sm{
    padding: 15px 28px;
}