.hero-area{
    background-image: url('../images/backgrounds/header-bg.png');
    background-size: cover;
    background-position: bottom center;
    margin-bottom: 200px;
    @include desktop-lg{
        margin-bottom: 100px;
    }
    @include desktop{
        margin-bottom: 50px;
    }
    @include tablet{
        margin-bottom: 25px;
    }
}

.hero-header{
    margin-bottom: 70px;
    @include desktop{
        margin-bottom: 40px;
    }
    h1,p{
        color: $white;
    }
    p{
        font-size: 23px;
        @include desktop{
            font-size: 16px;
        }
        @include mobile{
            font-size: 14px;
        }
    }
    h1{
        @include desktop{
            font-size: 45px;
        }
        @include mobile{
            font-size: 30px;
        }
        @include mobile-xs{
            font-size: 25px;
        }
    }
}

.insta-count{
    background: $white;
    border-radius: 4px;
    box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.05);
    padding: 20px 55px 70px;
    @include desktop-lg{
        padding: 20px 30px 70px;
    }
    @include mobile-xs{
        padding: 10px 20px 50px;
    }
    ul.nav-tabs{
        display: flex;
        border-bottom: 0;
        position: relative;
        &::before{
            content: '';
            position: absolute;
            bottom: 0;
            width: 100%;
            height: 4px;
            background-color: #d8d8d8;
            border-radius: 4px;
        }
        .nav-item{
            flex-basis: 50%;
            text-align: center;
            .nav-link{
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 20px 0;
                border-radius: 0;
                border-width: 0;
                position: relative;
                h5{
                    margin-bottom: 0;
                    margin-left: 10px;
                    color: $text-color;
                    font-weight: 400;
                    transition: .3s ease;
                    @include mobile-xs{
                        font-size: 14px;
                    }
                }
                .icon{
                    svg{
                        fill: $text-color;
                        transition: .3s ease;
                    }
                }
                &::before{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 4px;
                    background: $black;
                    border-radius: 4px;
                    left: 0;
                    bottom: 1px;
                    transform: scaleX(0);
                    transform-origin: right;
                    transition: transform .3s ease-in-out;
                }
                &.active{
                    background-color: transparent;
                    border-width: 0;
                    position: relative;
                    &::before{
                        transform: scaleX(1);
                        transform-origin: left;
                    }
                    h5{
                        color: $text-color-dark;
                    }
                    .icon{
                        svg{
                            fill: $text-color-dark;
                        }
                    }
                }
                &:hover{
                    border-width: 0;
                }
            }
        }
    }
    .user-details{
        margin-top: 55px;
        margin-bottom: 70px;
        img{
            border-radius: 50%;
            margin-bottom: 10px;
        }
        h2{
            @include mobile{
                font-size: 30px;
            }
        }
        .username{
            margin-bottom: 35px;
        }
    }
    .tab-pane.active{
        .fade-up-animation{
            animation: fading-anim .7s ease;
        }
    }
}

.checkform{
    padding: 0 50px;
    @include desktop-lg{
        padding: 0;
    }
    input{
        border: none;
        box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.1);
        &:focus{
            outline: 0;
            box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.1);
        }
        @include desktop{
            text-align: center;
        }
        @include mobile{
            font-size: 12px;
        }
    }
    button{
        box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.1);
        @include desktop{
            margin-top: 15px;
        }
    }
}

@keyframes fading-anim{
    0% {
        opacity: 0;
        transform: translateY(40px);
    }
    100%{
        opacity: 1;
        transform: translateY(0);
    }
}