// 1.1. Typography
@import url('https://fonts.googleapis.com/css?family=Roboto:300,400,500,700');

body {
  line-height: 1.2;
  font-family: $primary-font;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
}

p {
  letter-spacing: .5px;
  color: $text-color;
  font-size:16px;
  line-height: 1.5;
}

h1,h2,h3,h4,h5,h6 {
  color: $black;
  font-weight: 700;
}

h1, .h1{
  font-size: 54px;
}

h2, .h2{
  font-size: 46px;
}

h3, .h3{
  font-size: 40px;
}

h4, .h4{
  font-size: 23px;
}

h5, .h5{
  font-size: 19px;
}

h6, .h6{
  font-size: 16px;
}